        user1Name: '',
        user1Img: '',
        user1Praise: 0,
        author: '',
        authorPraise: 0,
        user2: '',
        user2Praise: 0,
        userCity: '',
      }
    },
    // 处理文件选择
    handleFileUpload(event: any) {
      const file = event.target.files[0]
      if (!file) return
      // 验证文件类型
      if (!file.type.startsWith('image/')) {
        alert('请选择图片文件！')
        return
      }
      if (file.size > this.maxSize) {
        alert('图片大小不能超过 1MB')
        return
      }
      // 将图片转为 Base64
      const reader = new FileReader()
      reader.onload = (e) => {
        this.AddAuthor.img = e.target == null ? '' : e.target.result
      }
      reader.readAsDataURL(file)
    },
    // 删除图片
    deleteImage() {
      this.AddAuthor.img = ''
      alert('图片已删除！')
    },
    async addAuthor() {
      if (!this.AddAuthor.img) {
        alert('请上传头像！')
      } else if (!this.AddAuthor.name) {
        alert('请填写用户名')
      } else {
        this.AddAuthor.id = Math.floor(Math.random() * 100)
        try {
          this.message = await this.db.addItem(this.AddAuthor)
          this.getData()
          this.cancelAddAuthor()
        } catch (error) {
          this.message = error
        }
        this.isAddAuthor = false
      }
    },
    cancelAddAuthor() {
      this.AddAuthor.img = ''
      this.AddAuthor.name = ''
      this.AddAuthor.provinces = ''
      this.isAddAuthor = false
    },
    async getData() {
      try {
        this.authorList = await this.db.getAllItems()
        this.message = 'Data retrieved'
      } catch (error) {
        this.message = error
      }
    },
    async deleteData(id: any) {
      try {
        this.message = await this.db.deleteItem(id)
        this.getData()
      } catch (error) {
        this.message = error
      }
    },
    generated() {
      this.authorList.forEach((item) => {
        if (item.id == this.selectedAuthor) {
          this.generatedData.selectedAuthor = item
        }
      })
      //背景色
      if (this.generatedData.selectedColor == 'white') {
        this.backgroundColor = '#fff'
        this.color = '#000'
      } else {
        this.color = '#fff'
        this.backgroundColor = '#000'
      }
      //随机头像
      this.generatedData.authorMode.user1Img =
        'https://picsum.photos/id/' + Math.floor(Math.random() * 1000) + '/50'
      this.fetchName1()
      //随机城市 provinces Math.floor(Math.random() * 32)
      this.generatedData.authorMode.userCity =
        this.provinces[Math.floor(Math.random() * 32)].name
      this.generatedData.dialogueList.forEach((item, index) => {
        item.userImg =
          'https://picsum.photos/id/' + Math.floor(Math.random() * 1000) + '/50'
        item.userCity = this.provinces[Math.floor(Math.random() * 32)].name
        this.fetchName(index)
      })
      this.isGenerated = !this.isGenerated
    },
    async fetchName(index: any) {
      const res = await axios.get('https://cn.apihz.cn/api/zici/sjwm.php', {
        params: {
          id: 88888888,
          key: 88888888,
        },
      })
      this.generatedData.dialogueList[index].userName = res.data.msg
    },
    async fetchName1() {
      const res = await axios.get('https://cn.apihz.cn/api/zici/sjwm.php', {
        params: {
          id: 88888888,
          key: 88888888,
        },
      })
      if (res.data.msg.length > 5) {
        this.fetchName1()
      } else {
        this.generatedData.authorMode.user1Name = res.data.msg
      }
    },
    back() {
      this.isGenerated = !this.isGenerated
      this.generatedData = {
        selectedColor: 'white', //颜色
        selectedAuthor: { provinces: '', name: '', img: '' }, //作者
        mode: 'true', //模式
        isSearching: '', //都在搜
        reviews: 0, //评论数
        authorMode: {
          user1: '',
          user1Name: '',
          user1Img: '',
          user1Praise: 0,
          author: '',
          authorPraise: 0,
          user2: '',
          user2Praise: 0,
          userCity: '',
        }, //3
        dialogueList: [
          {
            user: '',
            userName: '',
            userImg: '',
            userPraise: 0,
            author: '',
            authorPraise: 0,
            userCity: '',
          },
        ], //2
      }
    },
    async generateImage() {
      // 确保 DOM 渲染完成
      await this.$nextTick() // ‌:ml-citation{ref="1,3" data="citationList"}

      // 生成 Canvas
      const element = this.$refs.contentArea as HTMLElement
      const canvas = await html2canvas(element, {
        useCORS: true, // 处理跨域图片 ‌:ml-citation{ref="1,2" data="citationList"}
        scale: 2, // 提高分辨率（可选） ‌:ml-citation{ref="2,5" data="citationList"}
      })

      // 转换为 Base64 图片
      const imageUrl = canvas.toDataURL('image/png') // ‌:ml-citation{ref="4,5" data="citationList"}

      // 触发下载
      const link = document.createElement('a')
      link.href = imageUrl
      link.download = 'page-screenshot.png' // 自定义文件名 ‌:ml-citation{ref="4,6" data="citationList"}
      link.click()
    },
  },
})
</script>
