// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-nocheck
export default class IndexedDB {
	/**
	 * @param {string} dbName
	 * @param {string} storeName
	 */
	constructor(dbName, storeName) {
  this.dbName = dbName;
  this.storeName = storeName;
  this.db = null;
	}
  
	// 打开或创建数据库
	openDB() {
return new Promise((resolve, reject) => {
	const request = indexedDB.open(this.dbName, 1);

	request.onupgradeneeded = (event) => {
  const db = event.target.result;
  if (!db.objectStoreNames.contains(this.storeName)) {
	db.createObjectStore(this.storeName, { keyPath: "id" });
  }
	};

	request.onsuccess = (event) => {
  this.db = event.target.result;
  resolve(this.db);
	};

	request.onerror = (event) => {
  reject(`IndexedDB error: ${event.target.errorCode}`);
	};
});
	}
  
	// 添加数据
	/**
	 * @param {any} item
	 */
	addItem(item) {
  return new Promise((resolve, reject) => {
	const transaction = this.db.transaction([this.storeName], "readwrite");
	const store = transaction.objectStore(this.storeName);
	const request = store.add(item);

	request.onsuccess = () => resolve("Data added");
	request.onerror = (/** @type {{ target: { errorCode: any; }; }} */ event) => reject(`Error adding data: ${event.target.errorCode}`);
  });
	}
  
	// 获取数据
	/**
	 * @param {any} id
	 */
	getItem(id) {
  return new Promise((resolve, reject) => {
	const transaction = this.db.transaction([this.storeName], "readonly");
	const store = transaction.objectStore(this.storeName);
	const request = store.get(id);

	request.onsuccess = () => resolve(request.result);
	request.onerror = (/** @type {{ target: { errorCode: any; }; }} */ event) => reject(`Error getting data: ${event.target.errorCode}`);
  });
	}
  
	// 更新数据
	/**
	 * @param {any} item
	 */
	updateItem(item) {
  return new Promise((resolve, reject) => {
	const transaction = this.db.transaction([this.storeName], "readwrite");
	const store = transaction.objectStore(this.storeName);
	const request = store.put(item);

	request.onsuccess = () => resolve("Data updated");
	request.onerror = (/** @type {{ target: { errorCode: any; }; }} */ event) => reject(`Error updating data: ${event.target.errorCode}`);
  });
	}
  
	// 删除数据
	/**
	 * @param {any} id
	 */
	deleteItem(id) {
  return new Promise((resolve, reject) => {
	const transaction = this.db.transaction([this.storeName], "readwrite");
	const store = transaction.objectStore(this.storeName);
	const request = store.delete(id);

	request.onsuccess = () => resolve("Data deleted");
	request.onerror = (/** @type {{ target: { errorCode: any; }; }} */ event) => reject(`Error deleting data: ${event.target.errorCode}`);
  });
	}
  
	// 获取所有数据
	getAllItems() {
  return new Promise((resolve, reject) => {
	const transaction = this.db.transaction([this.storeName], "readonly");
	const store = transaction.objectStore(this.storeName);
	const request = store.getAll();

	request.onsuccess = () => resolve(request.result);
	request.onerror = (/** @type {{ target: { errorCode: any; }; }} */ event) => reject(`Error getting all data: ${event.target.errorCode}`);
  });
	}
  }
  